/* global graphql */

const _ = require('lodash');
import React from 'react';
import Breadcrumb from '../components/breadcrumb';
import YearSelect from '../components/year-selector';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import '../../node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';

class IndexPage extends React.Component {
	constructor(props) {
						super(props);
						this.yearChangeHandler = this.yearChangeHandler.bind(this);
						this.loadYearData = this.loadYearData.bind(this);
	        	this.state = {
								yearLabels: [],
								YearSelected: "",
								gamesByYear: [],
								statsByTeam: []

		        }
	        }
					
		yearChangeHandler = (event) => {
			var selected = event.target.value;
			this.setState(
				{ YearSelected: selected },
				() => this.loadYearData()
			)
		}
	
		loadYearData = () => {
			
				var year = this.state.YearSelected;
				var includeRegularSeason = this.refs.regular_season.checked;
				var includePlayoffs = this.refs.playoffs.checked;

				var allYearsObject = this.state.gamesByYear;
				var yearObject = this.state.gamesByYear.find(obj => obj.year === year);
				var gameWeeks = [];
				if (year == 'All') {
					allYearsObject.forEach(function (element) {
						let weeks = element.weeks;
						// Just regular season
						if (!includePlayoffs && includeRegularSeason) { 
							weeks = _.filter(weeks, { 'isPlayoff': false });
							gameWeeks.push(weeks);
						}

						//Just playoffs 
						else if (includePlayoffs && !includeRegularSeason) {
							weeks = _.filter(weeks, { 'isPlayoff': true });
							gameWeeks.push(weeks);
						}
						// All
						else if (includePlayoffs && includeRegularSeason) {
							gameWeeks.push(weeks);
						}		
					});
				}
				else {
					let weeks = yearObject.weeks;
					// Just regular season
					if (!includePlayoffs && includeRegularSeason) {
						weeks = _.filter(weeks, { 'isPlayoff': false });
						gameWeeks = weeks;
					}
					//Just playoffs 
					else if (includePlayoffs && !includeRegularSeason) {
						weeks = _.filter(weeks, { 'isPlayoff': true });
						gameWeeks = weeks;
					}
					// All
					else if (includePlayoffs && includeRegularSeason) {
						gameWeeks = weeks;
					}
				}
				var allGamesYear = [];
				var gamesByOwner = [];
				var statsByTeam = [];

				gameWeeks.reverse();

				//get all games from weeks
				if (year == 'All') {
					for (let i = 0; i < gameWeeks.length; i++) {
						gameWeeks[i].map((week) => {
							week.games.map((game) => {
								allGamesYear.push(game);
							})
						})
					}
				}
				else {
					gameWeeks.map((week) => {
						week.games.map((game) => {
							allGamesYear.push(game);
						})
					})
				}

				//get games by owner
				if(year == 'All') {
					allGamesYear.map((game) => {
						if (gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId)) {
							var teamObject = gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId);
								teamObject.games.push(game);
						} else {
							gamesByOwner.push({
								id: game.team_a["0"].ownerId,
								name: game.team_a["0"].owner,
								games: [game]
							})
						}
						if (gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId)) {
							var teamObject = gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId);
								teamObject.games.push(game);
						} else {
							gamesByOwner.push({
								id: game.team_b["0"].ownerId,
								name: game.team_b["0"].owner,
								games: [game]
							})
						}
					})
				} else { 
					allGamesYear.map((game) => {
					if(game.team_a["0"].name) {
						if (gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId)) {
							var teamObject = gamesByOwner.find(obj => obj.id === game.team_a["0"].ownerId);
								teamObject.games.push(game);
						} else {
							gamesByOwner.push({
								id: game.team_a["0"].ownerId,
								name: game.team_a["0"].name,
								games: [game]
							})
						}
					} 
					
					if(game.team_b["0"].name) {
						if (gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId)) {
							var teamObject = gamesByOwner.find(obj => obj.id === game.team_b["0"].ownerId);
								teamObject.games.push(game);
						} else {
							gamesByOwner.push({
								id: game.team_b["0"].ownerId,
								name: game.team_b["0"].name,
								games: [game]
							})
						}
					} 
				})
				}

				// process stats per team 

				gamesByOwner.map((team) => {
					var wins = 0,
						loses = 0,
						ties = 0,
						points = 0,
						pointsAgaisnt = 0;
					
					team.games.map((game) => {
						if(year == "All") {
							if(game.team_a[0].ownerId === team.id) {
								points += parseFloat(game.team_a[0].score);
								pointsAgaisnt += parseFloat(game.team_b[0].score);
							} else if (game.team_b[0].ownerId === team.id) {
								points += parseFloat(game.team_b[0].score);
								pointsAgaisnt += parseFloat(game.team_a[0].score);
							}
							if (
								(parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score)) && game.team_a[0].ownerId === team.id ||
								(parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score)) && game.team_b[0].ownerId === team.id
							)  {
								wins++
							} else if (parseFloat(game.team_b[0].score) === parseFloat(game.team_a[0].score)) {
								ties++
							} else {
								loses++
							}
						} else {
							if (game.team_a[0].ownerId === team.id) {
								points += parseFloat(game.team_a[0].score);
								pointsAgaisnt += parseFloat(game.team_b[0].score);
							} else if (game.team_b[0].ownerId === team.id) {
								points += parseFloat(game.team_b[0].score);
								pointsAgaisnt += parseFloat(game.team_a[0].score);
							}
							if (
								(parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score)) && game.team_a[0].ownerId === team.id ||
								(parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score)) && game.team_b[0].ownerId === team.id
							)  {
								wins++
							} else if (parseFloat(game.team_b[0].score) === parseFloat(game.team_a[0].score)) {
								ties++
							} else {
								loses++
							}
						}
					})

					var winRate = wins/team.games.length;

					// Get Owner name from Owners
					const owners = this.props.data.allDataJson.edges[0].node.owners;
					const owner = _.find(owners, ['id', team.id]);
					statsByTeam.push({
						team: owner.name + " (" + owner.fantasyname + ")" ,
						wins: wins,
						loses: loses,
						ties: ties,
						points: parseFloat(points.toFixed(2)),
						pointsAgainst: parseFloat(pointsAgaisnt.toFixed(2)),
						winRate: winRate.toFixed(3)
					})
				})

				statsByTeam.sort(function(a, b) {
					a = new Date(a.wins);
					b = new Date(b.wins);
					return a>b ? -1 : a<b ? 1 : 0;
				});

				statsByTeam.map((team, index) => {
					team.rank = index + 1;
				})

				this.setState(
					{ statsByTeam: statsByTeam }
				)
		}
	
		UNSAFE_componentWillMount() {
			const years = this.props.data.allDataJson.edges[0].node.years;
			var games = [];
			
			years.map((year) => {
				year.weeks_games.map((week) => {
					games.push(...week.games);
				})
			});

			var playOffWeeks = [];
			var gamesByYear = [];
			var yearLabels = [];
			
			// get games by years and weeks

			games.map((game) => {
				// If year repeats itself
				if(gamesByYear.find(obj => obj.year === game.year)) { 
					var currentYear = gamesByYear.find(obj => obj.year === game.year)
						if (currentYear.weeks.find(obj => obj.week === game.week)) {
							var currentWeek = currentYear.weeks.find(obj => obj.week === game.week)
							currentWeek.games.push(game);
						} else {
							currentYear.weeks.push({
								week: game.week,
								games: [game]
							})
						}
				} else { // Initial year 
					gamesByYear.push({
							year: game.year,
							weeks: [{
								week: game.week,
								games: [game]
							}]
					});
					yearLabels.push(game.year);
				}
			});

			// mark the playoff weeks and populate array
			gamesByYear.map((year) => {
				year.weeks.map((week) => {
					if (week.games[0].playoffTierType != 'NONE') {
						week.isPlayoff = true;
						playOffWeeks.push(week)
					}
					else {
						week.isPlayoff = false;
					}
				});
			});
			
			yearLabels.reverse();

			this.setState({yearLabels:yearLabels });
			this.setState({gamesByYear:gamesByYear})
			this.setState({YearSelected: 'All' })
			 
  	}        
  	
  	componentDidMount() {
		this.loadYearData();
	}
		
	regularSeasonClickHandler = event => {
		this.loadYearData();
	}
		
  	render() {
			
			//<Standings data={this.state.statsByTeam}/><BootstrapTable keyField='id' data={ products } columns={ columns } />
		 return (
		   <main className="container-fluid">
		    <Breadcrumb title="Standings"/>
			     <div className="card mb-3">
			        <div className="card-header"> 
						 <input type="checkbox" id="regular-season" ref="regular_season" value="regular-season" onChange={this.regularSeasonClickHandler} defaultChecked={true} /> <span className="checkbox-label">Regular Season</span>
						 <input type="checkbox" id="playoffs" ref="playoffs" value="playoffs" onChange={this.regularSeasonClickHandler} /> <span className="checkbox-label">Playoffs</span>
						<br/><br/>
						<YearSelect data={this.state.yearLabels} change={(event) => this.yearChangeHandler(event)}/>
			          </div>
			        <div className="card-body">
			          <div className="table-responsive">
								<BootstrapTable data={this.state.statsByTeam} striped hover version='4'>
									<TableHeaderColumn isKey dataField='rank' isKey={true} dataSort={true} width="10%">Rank</TableHeaderColumn>
									<TableHeaderColumn dataField='team' dataSort={ true }>Team</TableHeaderColumn>
									<TableHeaderColumn dataField='wins' dataSort={true} width="10%">W</TableHeaderColumn>
									<TableHeaderColumn dataField='loses' dataSort={true} width="10%">L</TableHeaderColumn>
									<TableHeaderColumn dataField='ties' dataSort={true} width="10%">T</TableHeaderColumn>
									<TableHeaderColumn dataField='winRate' dataSort={true} width="10%">PCT</TableHeaderColumn>
									<TableHeaderColumn dataField='points' dataSort={true} width="10%">PF</TableHeaderColumn>
									<TableHeaderColumn dataField='pointsAgainst' dataSort={ true } width="10%">PA</TableHeaderColumn>
								</BootstrapTable>
			          </div>
			        </div>
			        <div className="card-footer small text-muted"></div>
			      </div>
		   </main>);
	}
}
export default IndexPage;

export const pageQuery = graphql`
  query standingsQuery {
		allDataJson {
			edges {
				node {
					owners {
						name
						fantasyname
						id
						image
						status
					}
					years{
						weeks_games {
							games {
								week
								playoffTierType
								team_a {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								team_b {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								year
							}
						}
					}
				} 
			}
		}
}
`;



